.card-overview {
    margin: 15px 0;
}
.chunks-block i {
    margin-right: 5px;
}
.chunk-name {
    font-size: 10px;
}
.chunk-value {
    font-size: 12px;
}
.column {
    padding: 10px 15px;
    display: inline-block;
    border-radius: 5px;
}
.card-preview {
    font-size: 10px;
    border: 1px solid #439C69;
    border-radius: 3px;
    padding:15% 10%;
}
.chunks-block {
    margin-top: 10px;
}
.card-ending {
    font-size: 10px;
    margin-bottom: 5px;
}
.card-ending i {
    vertical-align: text-top;
    color: #439C69;
}
.card-number {
    /*margin: 10px 0px;*/
}
.card-name-info {
    float: left;
    text-align: left;
}
.card-balance-info {
    float: right;
    text-align: right;
}
.lp-normal-account-select-size .card-balance-info, .lp-small-account-select-size .card-balance-info {
    text-align: left;
    float: left;
}
.chunk-column {
    padding: 0px;
}

/* Card Design */
.card-design {
    width: 100%;
    /*max-width: 250px;*/
    height: auto;
    /*max-height: 156px;*/
    font-size: 100%;
    position: relative;
}
.card-design .card-image {
    width: 100%;
    height: auto;
}
.card-design .card-text {
    color: #fff;
    position: absolute;
    font-weight: normal;
    top: 0;
    left: 0;
    font-size: 1em;
    text-shadow: 0 1px 1px #999, 0 2px 1px #001135;
    letter-spacing: 0.1em;
}
.card-design .card-holder {
    top: 9.2em;
    left: 1.2em;
}
.card-design .card-number {
    top: 5.8em;
    left: 1.2em;
}
.card-design .card-expiry {
    top: 7.8em;
    left: 7.6em;
}



@media (max-width: 768px) {
    .card-overview .lp-normal-account-select-size, .card-overview .lp-large-account-select-size {
        width: 100%;
    }
    body .card-overview {
        margin-top: 0;
    }
    body .card-overview .card-design {
        margin-top: 20px;
        padding-left: 10px;
        padding-right: 10px;
    }
    body .card-overview .card-design img{
        width: 100%;
    }
    body .card-overview .card-design .card-text {
        display: block;
    }
    .card-overview .card-balance-info.h6 {
        margin-top: 30px;
        float: right;
    }
    .card-overview > div {
        padding: 0;
    }
    .card-overview .lp-account-select {
        top: 0;
    }
    .card-overview button.btn.dropdown-toggle.btn-default {
        height: 120px;
    }
}